body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.customNav {
  color: black;
  background-image: -webkit-linear-gradient(left, #857107 , #E0BF0B);
  background-image: linear-gradient(to right, #857107 , #E0BF0B)

}
.navbar-brand {
	font-size: 25px;
	color: black;
}
nav h3{
	position: relative;
	right: 100px;
	display: inline-block;
}
#status {
	left: 35px;
}

.para {
	font-weight: bold;
}
.imgDisplay {
	width: 150px;
	height: 150px;
	background-color: #E0BF0B;
	float: right;
	margin: 1px;
	border: 2px ridge #ddd; /* Gray border */
	border-radius: 4px;  /* Rounded border */
	padding: 5px; /* Some padding */


}



img:hover {
  box-shadow: 0 0 2px 1px rgba(0, 140, 186, 0.5);
}

.footer{
    background-color: #666666;
    
    padding: 20px;
    position: fixed;
    bottom: 0;
    margin-top: 25px;
    border-top:2px solid #E0DF0B;
}
.footContent{
    font-family: arial;
    font-size: 10px;
    text-align: center;
    color: #dddddd;
    position: absolute;
    left: 46%;
    top: 40%;
}
.position {
	border: 1px solid none;
	width:  40%;
	position: absolute;
	left: 290px;
	margin: 100px ;

}
